<template>
  <div>
    <!--hero-->
    <horizontal-banner>
      <template slot="content">
        <h1 class="heading">    </h1>
        <p class="my-4 my-md-7">                                           .</p>
        <v-btn
            :to="{
              name: 'SearchDoctors'
            }"
            class="exclusive"
            color="primaryLight"
            elevation="0"
            x-large
        > ...
        </v-btn>
      </template>
    </horizontal-banner>

    <!--statistics-->
    <v-row v-if="info" class="py-10 py-md-16" justify="center" no-gutters>
      <v-col cols="9">
        <v-card
            class="pa-10"
            elevation="0"
        >
          <v-row no-gutters>
            <v-col cols="12" md="4">
              <statistic-card
                  :num="info.patients_count>0?'+'+info.patients_count:info.patients_count"
                  desc="  "
              />
            </v-col>
            <v-col cols="12" md="4">
              <statistic-card
                  :num="info.experts_count>0?'+'+info.experts_count:info.experts_count"
                  bordered
                  desc=" "
              />
            </v-col>
            <v-col cols="12" md="4">
              <statistic-card
                  :num="info.done_visits_count>0?'+'+info.done_visits_count:info.done_visits_count"
                  desc="  "
              />
            </v-col>
          </v-row>
        </v-card>
      </v-col>
    </v-row>

    <!--whats diabet-->
    <horizontal-banner
        :img="require(`@/assets/media/img/dibet.webp`)"
        inverse
    >
      <template slot="content">
        <h2 class="heading"> </h2>
        <p class="my-4 my-md-7">    .                .               .                         .                           .            .</p>
      </template>
    </horizontal-banner>

    <!--latest articles-->
    <v-row v-if="articles && articles.length>0" class="py-10 py-md-16" justify="center" no-gutters>
      <v-col cols="11">
        <section-head title=" "/>

        <card-slider
            :autoplay="true"
            :interval="3000"
            :loop="false"
            :navigation="true"
            :pagination="false"
            :speed="400"
            :start="cardSliderStarter"
            lg-view="4"
            md-view="3"
            name="articles"
            pagination-color="dark"
            sm-view="2"
            xl-view="5"
            xs-view="1"
        >
          <template slot="slides">
            <article-card
                v-for="(item, i) in articles"
                :key="`article-${i}`"
                :desc="item.desc"
                :link="{
                  name: 'ArticlesShow',
                  params: {
                    id: item.id
                  }
                }"
                :title="item.title"
                class="swiper-slide"
                slide
            />
          </template>
        </card-slider>
      </v-col>
    </v-row>

    <!--free plan-->
    <horizontal-banner
        :img="require(`@/assets/media/img/freeplan.webp`)"
    >
      <template slot="content">
        <h2 class="heading primaryLight--text">     !!!</h2>
        <p class="my-4 my-md-7">                                       !</p>
        <v-btn
            :to="{
              name: 'FreePlan'
            }"
            class="exclusive"
            color="primaryLight"
            elevation="0"
            x-large
        > ...
        </v-btn>
      </template>
    </horizontal-banner>
  </div>
</template>

<script>
import HorizontalBanner from "../components/parts/HorizontalBanner";
import StatisticCard from "../components/cards/StatisticCard";
import ArticleCard from "../components/cards/ArticleCard";
import CardSlider from "../components/parts/CardSlider";
import SectionHead from "../components/parts/SectionHead";

export default {
  name: "LandingPage",
  components: {SectionHead, CardSlider, ArticleCard, StatisticCard, HorizontalBanner},
  data: () => ({
    cardSliderStarter: 0,
    info: false,
    articles: false
  }),
  computed: {},
  mounted() {
  },
  created() {
    //get info
    this.$store.getters.api({
      method: 'get',
      url: `api/v1/diacare_history/`,
      data: false,
      loader: true
    }).then((r) => {
      this.info = {
        "patients_count": r.data.patients_count,
        "experts_count": r.data.experts_count,
        "done_visits_count": r.data.done_visits_count
      }
    })

    //get articles
    this.$store.getters.api({
      method: 'get',
      url: `api/v1/article/retrieve/?limit=10&offset=0`,
      data: false,
      loader: true
    }).then((r) => {
      this.articles = []
      r.data.results.forEach((item) => {
        var desc = item.context.slice(1, 255).split(' ')
        desc.pop()
        this.articles.push({
          title: item.subject,
          desc: desc.join(' ') + '...',
          id: item.id
        })
        setTimeout(() => {
          this.cardSliderStarter = this.cardSliderStarter + 1
        }, 200)
      })
    })


  }
}
</script>

<style scoped>
</style>